@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
/* Global */

* {
  font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji,
    Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
}

/* Chats Page */  

.chats-page {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.nav-bar {
  width: 100%;
  height: 66px;
  background-color: #0cbaba;
  background-image: linear-gradient(315deg, #0cbaba 0%, #380036 74%);

}


.logo-tab {
  position: absolute;
  left: 22px;
  top: 12px;
  font-size: 32px;
  font-weight: 700;
  color: white;
  cursor: pointer;
}

.logout-tab {
  position: absolute;
  top: 22px;
  right: 22px;
  color: white;
  cursor: pointer;
  font-weight: bold;
}


::-webkit-scrollbar {
  width: 0; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}

#loading-container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: rgb(255, 255, 255, 0.22);
}

#loading-icon {
  font-size: 66px;
  position: relative;
  left: calc(50vw - 33px);
  top: calc(50vh - 22px);
}

@media only screen and (max-width: 600px) {
  .ce-primary-button{
    position: absolute;
    left: 16rem;
    bottom: .5rem;
    
}
}

.login {
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;
    background-image: linear-gradient(#40a9ff, #096dd9);
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
}

.login__text > h1 {
    text-transform: uppercase;
    background-image: linear-gradient(315deg, #0cbaba 0%, #380036 74%);
    background-clip: text;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
}

.login__container {
    padding: 70px;
    text-align: center;
    background-color: white;
    border-radius: 10px;
    box-shadow: -1px 18px 48px 4px rgb(0 0 0 / 73%);

}

.login__container > img {
    object-fit: contain;
    height: 100px;
    margin-bottom: 40px;
}

.login__container > button {
    margin-top: 20px;
    text-transform: inherit !important;
    background-color: #098c91 !important;   
    color: white;
    font-weight: bold;
    width: 100%;
}

.login__container > button:hover {
    background-color: #065b5e !important; 
}
